.blog-card {
  position: relative;
  display: flex;
}

.blog-card img {
  display: block;
}

.blog-card__textgroup {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  min-width: 50%;
  background-color: #f0eae4;

  &--big {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 47%;
  }

  &--inverse {
    order: 1;
  }
}

.blog-card__textgroup p {
  margin: 0;
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 18px;
  color: #9b9997;
  order: -1;
}

.blog-card__textgroup h3 {
  margin: 0;
  margin-bottom: 10px;
  max-width: 163px;
  font-family: $second-font;
  font-weight: 600;
  font-size: 26px;
  line-height: 34px;
  text-align: center;
}

.blog-card__link {
  padding-top: 3px;
  padding-left: 5px;
  padding-right: 5px;
  color: #221b17;
  border-bottom: 1px solid #a87b62;
}

@media (max-width: $tablet-width) {
  .blog-card img {
    width: 339px;
  }

  .blog-card__textgroup {
    &--big {
      position: static;
      height: auto;
      order: 1;
    }
  }
}

@media (max-width: $mobile-width) {
  .blog-card img {
    width: 737px;
  }

  .blog-card {
    flex-direction: column;
  }

  .blog-card__textgroup {
    min-height: 256px;
    order: 1;
  }
}
