.products {
  margin-top: 130px;
  margin-bottom: 100px;
  text-align: center;
}

.products p {
  margin: 0;
  margin-bottom: 20px;
  font-family: $second-font;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  text-align: center;
  color: #1d1613;
}

.products__link {
  padding-top: 3px;
  padding-left: 5px;
  padding-right: 5px;
  color: #221b17;
  border-bottom: 1px solid #a87b62;
}

.products__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 30px;
  padding: 0;
  list-style: none;
}

.products__buttons {
  text-align: center;
}

@media (max-width: $tablet-width) {
  .products__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
