.blog {
  margin-bottom: 70px;
  text-align: center;
}

.blog h2 {
  margin: 0;
  margin-bottom: 10px;
  font-family: $second-font;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  text-align: center;
  color: #1d1613;
}

.blog__link {
  padding-top: 3px;
  padding-left: 5px;
  padding-right: 5px;
  color: #221b17;
  border-bottom: 1px solid #a87b62;
}

.blog__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  row-gap: 30px;
  column-gap: 30px;
  margin: 0 auto;
  margin-top: 30px;
  padding: 0;
  max-width: 1400px;
  list-style: none;
}

.blog__item {
  &--small {
    grid-column: 1 / 2;
  }

  &--big {
    grid-row: 1 / -1;
    grid-column: 2 / -1;
    margin-left: auto;
  }
}

@media (max-width: $tablet-width) {
  .blog__list {
    display: block;
  }

  .blog__item {
    margin-bottom: 50px;
  }
}

@media (max-width: $mobile-width) {
  .blog__item {
    margin-bottom: 40px;
  }
}